import { transformResponse } from ".";
import { IAllSettings } from "../modules/SettingsModule";
import {
  IServerResponse,
  Language,
  ReportLanguage,
  UpdateCheckInterval,
  EmailNotification,
  IUpdateUserSettingsResponseSuccess,
  IUpdateUserSettingsArgs
} from "@/shared/types";
import { axiosInstance } from "./axios";

const fallbackSettings: IAllSettings = {
  gui: {
    language: Language.en,
    showSnackbar: true,
    theme: {
      dark: true,
      primary: "#456CB2",
      secondary: "#424242",
      accent: "#03CC00"
    }
  },
  userData: {
    email: "",
    firstName: "",
    lastName: "",
    username: "user"
  },
  notification: {
    from: "",
    hostname: "",
    password: "",
    port: 0,
    username: "",
    recipient: [] as string[],
    reportLanguage: ReportLanguage.gui,
    sendEmails: true
  },
  general: {
    automaticUpdates: true,
    enableRegister: false,
    updateCheckInterval: UpdateCheckInterval.daily
  },
  backup: {
    defaultEmailNotification: EmailNotification.always,
    defaultMaxBackupsPerRepo: 999,
    defaultUnlockTime: 60 * 5 // 5 hours (time in minutes)
  }
};

export const getSettings = async (
  userId: number
): Promise<IServerResponse<IUpdateUserSettingsResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IUpdateUserSettingsResponseSuccess>
  >(`/user/${userId}/usersettings`);
  return transformResponse(response);
};

export const saveSettings = async (
  userId: number,
  settings: IUpdateUserSettingsArgs
): Promise<IServerResponse<IUpdateUserSettingsResponseSuccess>> => {
  const response = await axiosInstance.put<
    IServerResponse<IUpdateUserSettingsResponseSuccess>
  >(`/user/${userId}/usersettings`, settings);
  return transformResponse(response);
};
